export default (isEdit) => {
  let eventFormContent = (isEdit) ?
      `<a href="#" class="button button--gray" data-cancel></a>
      <div class="event-form__delete-btn">
          <button class="button button--gray" id="deleteEventBtn"> </button>
      </div>
      <button class="button button--blue" id="editEventBtn"></button>`
    : `<a href="#" class="button button--gray" data-cancel></a>
      <button class="button button--blue button--disabled" id="createBtn"> </button>`;

  return eventFormContent;
};



